/* -------------------------------- 

Title: Pagination 
Descr: Component used to navigate through pages of related content

-------------------------------- */

:root {
  --pagination-item-padding: var(--space-xs);
  --pagination-item-gap: var(--space-xxs);
}
.pagination {
  width: 100%;
  margin: 0 auto;
  display: flex;
  justify-content: center;
  font-size: var(--text-sm, 0.833em);
  margin-bottom: var(--space-xxl);
}

.pagination__list {
  display: flex;
  flex-wrap: wrap;
  margin-bottom: calc(-1 * var(--pagination-item-gap));

  > * {
    margin-right: var(--pagination-item-gap);
    margin-bottom: var(--pagination-item-gap);
    align-items: center;
    display: inline-block; // flex fallback
    display: inline-flex;
  }

  > *:last-child {
    margin-right: 0;
  }
}
.page-item.next,
.page-item.prev {
  transition: background-color .6s cubic-bezier(.19,1,.22,1);
  svg {
    width: 20px;
    height: 20px;
    color: var(--color-primary);
  }

}
.page-numbers {
  margin: 0 4pt;
}
.page-numbers,
.post-page-numbers,
.page-item.next a,
.page-item.prev a,
.pagination__item {
  display: inline-block; // flex fallback
  display: inline-flex;
  align-items: center;
  white-space: nowrap;
  line-height: 1;
  font-size: 16px;
  font-weight: 600;
  padding-top: var(--pagination-item-padding);
  padding-bottom: var(--pagination-item-padding); 
  padding-left: calc(1.233 * var(--pagination-item-padding));
  padding-right: calc(1.233 * var(--pagination-item-padding));
  border-radius: 50px;
  text-decoration: none;
  list-style: none;
  color: var(--color-contrast-high);
  height: 45px;
  min-width: 45px;
  display: flex;
  justify-content: center;
  background-color: var(--color-contrast-lower);
  transition: all .6s cubic-bezier(.19,1,.22,1);

  &:not(.current):not(.dots):not(ul.page-numbers):hover {
    color: var(--color-contrast-higher);
    filter: contrast(.9);
  }

  &:not(ul.page-numbers):active {
    background-color: var(--color-contrast-low);
  }

  .icon {
    display: inline-block;
    color: currentColor;
  }
}
.next.page-numbers,
.prev.page-numbers {
    padding-left: calc(2* var(--pagination-item-padding));
    padding-right: calc(2* var(--pagination-item-padding));
}
.page-links-title {
  margin-right: 4pt;
}
.entry-content .page-links {
  display: flex;
    a {
        transition: all .3s cubic-bezier(.23,1,.32,1) !important;
    }
  a:after {
    content: none !important;
  }
}
.pagination__item {
  color: inherit;
}
.post-page-numbers.current,
.page-numbers.current,
.page-item.active a {
  background-color: var(--color-primary);
  color: var(--color-contrast-high);
  @include fontSmooth;

  &:not(.current):not(.dots):hover {
    background-color: var(--color-contrast-high);
    color: var(--color-contrast-lower);
  }

  &:active {
    opacity: .75;
  }
}
.pagination__list li:not(.next):not(.prev) a,
.display--sm a {
    width: 45px;
    height: 45px;
}

.pagination__list li:not(.active):hover a {
    filter: contrast(.9);
    color: var(--color-contrast-high);
}
.pagination__item--disabled {
  opacity: 0.5;
  pointer-events: none;
}

.pagination__jumper {
  align-items: center;
  justify-content: center;

  .form-control {
    width: 3.2em;
    margin-right: var(--space-xs);
    padding: var(--pagination-item-padding);
  }

  em {
    flex-shrink: 0;
    white-space: nowrap;
  }
}

.page-links {
  margin-top: calc(var(--space-md) * var(--text-vspace-multiplier));
  margin-bottom: calc(var(--space-md) * var(--text-vspace-multiplier));
  .post-page-numbers {
    margin-left: var(--space-xxs);
    text-decoration: none !important;
  }
}

// Woo
.woocommerce-pagination {
  margin-top: 8vh;
  margin-bottom: 8vh;
  > ul.page-numbers {
    background-color: transparent;
  }
}

// Dark Mode
body[data-theme='dark'] {
    .page-item.active a, .page-numbers.current, .post-page-numbers.current {
        color: var(--color-contrast-lower);
    }
}