// Animation Headline Widget
.ms-ah-wrapper {

  [data-effect1] .char {
    opacity: 0;
  }

  .content__title {
    opacity: 0;
    font-family: var(--font-heading);
    &[data-effect3] {
        .word,
        .char {
          will-change: opacity;
        }
    }
  }


  .word,
  .char {
    will-change: transform, opacity;
  }

}