.ms-s-w {
    display: flex;
    flex-wrap: wrap;
    gap: 10px;
}
.ms-s-i {
    display: inline-flex;

    i {
        will-change: transform;
        transform: translateZ(0);
        backface-visibility: hidden;
        transition: transform 320ms cubic-bezier(.575,.015,0,.995);
    }

    &:hover i {
        transform: translateY(-4px);
    }
}
.ms-s-i.mono i {
    color: var(--color-contrast-higher);
}
.ms-s-i.s-it span {
    text-transform: capitalize;
    font-family: 'Neue Haas Grotesk Display Pro'!important;
}
.ms-s-i.s-it i {
    display: flex;

    &::before {
        margin-right: 6px;
    }
}

.ms-s-i.s-text i {
  text-transform: capitalize;
  font-size: 18px;

  span {
    font-family: 'Neue Haas Grotesk Display Pro'!important;
  }

  &::before {
    content: none;
  }
}
.ms-s-i i {
    font-size: 22px;
    display: flex;
    align-items: center;
    justify-content: center;
}
.ms-s-i.small {
    font-size: 18px;
        i {
            font-size: 18px;
        }
}
.ms-s-i.medium {
  font-size: 22px;
  i {
    font-size: 22px;
  }
}
.ms-s-i.large {
  font-size: 28px;
  i {
    font-size: 28px;
  }
}
.ms-s-i.large + .ms-s-i.large {
  margin-left: 18px;
}
