.ms-text-ticker {
    overflow: hidden;
}
.ms-tt-wrap {
    width: 100%;
    box-sizing: content-box;
    display: flex;
  
    .ms-tt {
      display: flex;
      align-items: center;
      white-space: nowrap;
      padding-left: 0;
      width: 100%;
      position: relative;
      box-sizing: content-box;
      font-family: var(--font-heading);
      transform: translate(-10vw);
    
      &__text {
  
        display: inline-block;
        color: var(--color-contrast-higher);  
        &.img {
            display: flex;
            align-items: center;
            justify-content: center;
        }

      }
  
    }
}

.ms-tt-wrap[data-scroll="off"][data-direction="ltr"] {

    .ms-tt {
        animation-direction: reverse;
    }

}
.ms-tt-wrap[data-sh="on"]:hover {
    .ms-tt {
        animation-play-state: paused;
    }
}

.ms-tt__text,
.ms-tt__text span {
  -webkit-text-stroke-color: var(--color-contrast-higher);
}

.ms-tt-wrap.s-d {
    
    &[data-direction='right'] {
        --scroll-speed: calc(var(--speed) * 1%);
        --tick: calc(var(--progress) * var(--scroll-speed));
        flex-direction: row-reverse;
    } 
    &[data-direction='left'] {
        --scroll-speed: calc(var(--speed) * -1%);
        --tick: calc(var(--progress) * var(--scroll-speed));
    }
    
    .ms-tt {
        transform: translateX(var(--tick));
    }
}