// Audio
.mejs-container {
  margin-bottom: calc(var(--space-md) * var(--text-vspace-multiplier));
}
.mejs-container,
.mejs-container .mejs-controls {
  background: var(--plyr-audio-control-color,#4a5464) !important;
  border-radius: 4px;
}

figure.wp-block-image + h4,
figure.wp-block-image + h5 {
  margin-bottom: 0;
}

/*
Transition Animation Mokko
*/

body.no-transition-effect{
    .portfolio-feed .item--inner figure,
    .portfolio-feed .item--inner .ms-p-video {
        --border-radius-p: 0px;
        clip-path: inset(0% 0% 0% 0% round var(--border-radius-p) var(--border-radius-p) var(--border-radius-p) var(--border-radius-p));
    }
}

// Portfolio Grid
body.transition-effect {

    // Portfolio Filtert
    .filter-nav--expanded .filter-nav__nav{
        --border-radius-medium: 40px;
        transition: clip-path .5s cubic-bezier(.38,.005,.215,1);
        clip-path: inset(0% 100% 0% 0% round var(--border-radius-medium) var(--border-radius-medium) var(--border-radius-medium) var(--border-radius-medium));

        .filter-nav__item {
            opacity: 0;
            transform: translateX(0px);
            transition: opacity .6s cubic-bezier(.38,.005,.215,1), transform .6s cubic-bezier(.38,.005,.215,1);
        }

    }

    .portfolio-feed .item--inner figure,
    .portfolio-feed .item--inner .ms-p-video {
        --border-radius: 30px;
        opacity: 1;
        clip-path: inset(35% 35% 35% 35% round var(--border-radius) var(--border-radius) var(--border-radius) var(--border-radius));
        transition: clip-path .5s cubic-bezier(.38,.005,.215,1);
    
        img, video {
            opacity: 0;
            transition: opacity 1s cubic-bezier(.38,.005,.215,1);
        }
    }

    .portfolio-feed .item--inner figure::before {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        z-index: 2;
        opacity: 1;
        will-change: opacity;
        background-color: var(--color-bg);
        transition: opacity .3s cubic-bezier(.38,.005,.215,1), background-color .3s cubic-bezier(.38,.005,.215,1);
    }

    .ms-p-content {
        opacity: 0;
    }

    .btn-wrap.ajax-area {
        opacity: 0;
    }

    .portfolio-feed .ms-p-content__inner h3::after {
        opacity: 0;
    }
}

// Loaded
body.lazy-load {

    // Portfolio Filtert
    .filter-nav--expanded .filter-nav__nav{
        --border-radius-medium: 40px;
        clip-path: inset(0% 0% 0% 0% round var(--border-radius-medium) var(--border-radius-medium) var(--border-radius-medium) var(--border-radius-medium));

        .filter-nav__item {
            transform: translateX(0px);
            opacity: 1;
            transition: opacity .6s cubic-bezier(.38,.005,.215,1), transform .6s cubic-bezier(.38,.005,.215,1);
        }

    }

    // Portfolio Grid
    .portfolio-feed .item--inner figure,
    .portfolio-feed .item--inner .ms-p-video {
        opacity: 1;
        --border-radius-p: 0px;
        clip-path: inset(0% 0% 0% 0% round var(--border-radius-p) var(--border-radius-p) var(--border-radius-p) var(--border-radius-p));
        transition: clip-path .5s cubic-bezier(.38,.005,.215,1) .25s;
        img, video {
            opacity: 1;
            transition: opacity 1s cubic-bezier(.38,.005,.215,1) 1s;
        }
        
    }
    .portfolio-feed .item--inner figure::before {
        opacity: 0;
        background-color: var(--color-contrast-lower);
        transition: opacity 1s cubic-bezier(.38,.005,.215,1) .75s, background-color .65s cubic-bezier(.38,.005,.215,1);
    }
    .ms-p-content {
        // transform: translateZ(0);
        opacity: 1;
        will-change: opacity;
        transition: opacity .5s .55s cubic-bezier(.38,.005,.215,1);
    }

    .btn-wrap.ajax-area {
        opacity: 1;
        will-change: opacity;
        transition: opacity .5s 1s cubic-bezier(.38,.005,.215,1);
    }

    .portfolio-feed .ms-p-content__inner h3::after {
        opacity: 1;
        transition: opacity 1s .75s cubic-bezier(.38,.005,.215,1);
    }
}

.ms-footer {
    overflow: hidden;
}

// Disable scroll param
html.lenis {
    scroll-behavior: auto !important;
}
