.woocommerce-account {
    background-color: var(--color-bg-woo);
}
// Navigation
.ms-woocommerce-MyAccount-navigation {
    margin-bottom: 2rem;
    ul {
        list-style: none;
        padding-left: 0;

        .woocommerce-MyAccount-navigation-link {
            padding: calc(.5 * var(--global--spacing-unit)) 0;
            border-bottom: solid 1px var(--color-contrast-low);

            a {
                color: var(--color-contrast-medium);

                &:hover {
                    color: var(--color-contrast-higher);
                }
            }
        }
        .woocommerce-MyAccount-navigation-link.is-active {
            a {
                color: var(--color-contrast-higher);
            }
        }
        .woocommerce-MyAccount-navigation-link:first-child {
            padding-top: 0;
        }
        .woocommerce-MyAccount-navigation-link:last-child {
            border-bottom: 0;
        }
    }
}

// Content
.ms-woocommerce-MyAccount-content {
    background-color: var(--color-bg-woo-white);
    padding: 2.5rem !important;
    border-radius: 40px;
    a {
        color: var(--color-contrast-higher);
        position: relative;
        z-index: 1;
        &::after {
            content: '';
            display: block;
            position: absolute;
            background-color: var(--color-primary);
            bottom: 0;
            left: 0;
            width: 100%;
            height: 1px;
            z-index: -1;
            transition: height .3s cubic-bezier(.23,1,.32,1);
        }
        &:hover::after {
            height: 4px;
        }
    }

    p {
        margin-bottom: 30px;
        line-height: 28px;
    }

    .woocommerce-orders-table {
        thead {
            text-align: left;
            background-color: var(--color-contrast-lower);
        }
    }

    .woocommerce-Addresses {
        display: flex;

        .u-column1.woocommerce-Address{
            margin-right: 4rem;
            margin-bottom: 2rem;
        }
        .woocommerce-Address {
            width: auto;

            address {
                line-height: 28px;
                font-style: italic;
            }
        }
    }

    // Form
    .woocommerce-EditAccountForm {
        em {
            font-size: 14px;
            color: var(--color-contrast-medium);
        }
        fieldset {
            border: none;
            padding: 0;
        }
        legend {
            font-size: 24px;
            font-weight: 700;
           
        }
    }
    .woocommerce-address-fields,
    .woocommerce-EditAccountForm {

        .button {
            width: 100%;
            height: 45px;
            border: none;
            width: auto;
            color: var(--color-contrast-higher);
            background-color: var(--color-primary);
            border-radius: 40px;
            width: auto;
            padding: 0 1.20rem;
            font-weight: 700;
        }

    }
}

// Login
.ms-woocommerce-form-login-wrapper {
    margin-bottom: calc(var(--global--spacing-vertical) + 8vh);
}
.ms-login-my-account,
.ms-register-account,
.ms-woocommerce-form-login {
    max-width: 28.125rem;
}

.ms-woocommerce-form-login,
.woocommerce-form-register,
.woocommerce-ResetPassword {
    margin: 2rem 0;
    margin-bottom: 8vh;

    .woocommerce-form-login__rememberme {
        margin-bottom: 1rem !important;

        .woocommerce-form__input-checkbox {
            margin-right: 4pt;
        }
    }
    p.form-row {
        display: flex;
        flex-direction: column;
        margin-bottom: 1.5rem;

        label {
            margin-bottom: 4pt;
            font-size: 14px;

            .required {
                text-decoration: none;
            }
        }

        .input-text  {
            width: 100%;
            padding: 0.5em 1.2em 0.5em 1.2em;
            height: 45px;
            border: none;
            border-radius: 40px;
            height: 45px;
            color: var(--color-contrast-higher);
            border: solid 1px transparent;
            background-color: var(--color-bg);
            transition: border-color 150ms var(--ease-in-out),box-shadow 150ms var(--ease-in-out),-webkit-box-shadow 150ms var(--ease-in-out);

            &:hover {
                border-color: var(--color-primary);
            }

            &:focus-visible {
                z-index: 1;
                border-color: var(--color-primary);
                -webkit-box-shadow: 0 0 0 4px hsla(var(--color-primary-h),var(--color-primary-s),var(--color-primary-l),.2);
                box-shadow: 0 0 0 4px hsla(var(--color-primary-h),var(--color-primary-s),var(--color-primary-l),.2);
                outline: 0;
            }
        }
    }

    .button {
        width: 100%;
        height: 45px;
        border: none;
        width: auto;
        color: var(--color-white);
        background-color: var(--color-primary);
        border-radius: 40px;
        width: auto;
        padding: 0 1.2rem;
    }
    a {
        color: var(--color-contrast-higher);
        &::after {
            content: '';
            display: block;
            position: absolute;
            background-color: var(--color-primary);
            bottom: 0;
            left: 0;
            width: 100%;
            height: 1px;
            z-index: -1;
            -webkit-transition: height .3s cubic-bezier(.23,1,.32,1);
            transition: height .3s cubic-bezier(.23,1,.32,1);
        }
        &:hover::after {
            height: 4px;
        }
    }
}

.woocommerce-form-register,
.woocommerce-ResetPassword {
    width: auto;

    p {
        margin-bottom: 1.5rem;
    }

}

.ms-dashboard-content {
    margin-bottom: 8vh;
}

.woocommerce-MyAccount-navigation {

    ul {
        list-style: none;
        padding: 2rem 2.5rem;
        border-radius: 40px;
        background-color: var(--color-bg-woo-white);
        li:first-child {
            padding-top: 0;
        }
        .woocommerce-MyAccount-navigation-link {
            padding-top: 8pt;
            padding-bottom: 8pt;
            a {
                display: flex;
                align-items: center;
                justify-content: space-between;
                color: var(--color-contrast-higher);

                &:hover {
                    color: var(--color-primary);
                }
            }
        }
        .woocommerce-MyAccount-navigation-link:last-child {
            padding-bottom: 0;
        }
    }
}

.woocommerce-MyAccount-navigation ul {
    li a::after {
        content: '';
        width: 20px;
        height: 20px;
        display: block;
        opacity: .4;
        background-repeat: no-repeat;
        background-size: contain;
    }
    .woocommerce-MyAccount-navigation-link.woocommerce-MyAccount-navigation-link--dashboard a::after {
        background-image: url(data:image/svg+xml;base64,);
    }
    .woocommerce-MyAccount-navigation-link.woocommerce-MyAccount-navigation-link--orders a::after {
        background-image: url(data:image/svg+xml;base64,);
    }
    .woocommerce-MyAccount-navigation-link.woocommerce-MyAccount-navigation-link--downloads a::after {
        background-image: url(data:image/svg+xml;base64,);
    }
    .woocommerce-MyAccount-navigation-link.woocommerce-MyAccount-navigation-link--edit-address a::after {
        background-image: url(data:image/svg+xml;base64,);
    }
    .woocommerce-MyAccount-navigation-link.woocommerce-MyAccount-navigation-link--edit-account a::after {
        background-image: url(data:image/svg+xml;base64,);
    }
    .woocommerce-MyAccount-navigation-link.woocommerce-MyAccount-navigation-link--customer-logout a::after {
        background-image: url(data:image/svg+xml;base64,);
    }

}

body[data-theme='dark'] {
    .woocommerce-MyAccount-navigation ul li a::after {
            filter: invert(1);
    }
}

@media (max-width: 768px) {
    .ms-woocommerce-MyAccount-content {
        padding-left: calc(var(--bs-gutter-x)/ 2);
        margin-top: 3rem;
    }
}