.ms-cc_p {
    pointer-events: none;
    cursor: pointer;
    position: fixed;
    top: 0px;
    left: 0px;
    width: 45px;
    height: 45px;
    z-index: 5;
    border-radius: 50%;

    .cursor-view {
        position: relative;
        left: 0;
        top: 0;
        right: 0;
        bottom: 0;
        z-index: 999;
        display: flex;
        overflow: hidden;
        width: 45px;
        height: 45px;
        justify-content: flex-start;
        align-items: center;
        flex: 0 0 auto;
        grid-auto-columns: 1fr;
        grid-template-columns: 1fr 1fr;
        grid-template-rows: auto auto;
        border: 0 #fff;
        border-radius: 100vw;
        color: #fff;
        line-height: 1;
        font-size: 18px;
        text-transform: uppercase;
        opacity: 0;
        backdrop-filter: blur(10px) brightness(100%);
        transition: all 700ms cubic-bezier(.23, 1, .32, 1);
        &::before {
            content: '';
            position: absolute;
            left: 0;
            right: 0;
            width: 100%;
            height: 100%;
            background-color: var(--color-contrast-higher);
            opacity: .4;
        }
        .cursor-text{
            position:relative;
            transform:translate(101%,0);
            flex-shrink: 0;
            transition-delay: 0.1s;
            transition: transform 700ms cubic-bezier(.23, 1, .32, 1);
        }

    }
    .cursor-text-holder{
        position:absolute;
        left:2.5em;
        overflow:hidden;
        display: flex;
    }

    .cursor-dot{
        position:absolute;
        left:.6vw;
        width:14px;
        height:14px;
        border-radius:100%;
        background-color:var(--color-white)
    }

}
// Active
.ms-cc_p.active .cursor-view {
    opacity: 1;
    transition: all 700ms cubic-bezier(.23, 1, .32, 1);
}
.ms-cc_p.active .cursor-view .cursor-text {
    position: relative;
    transform: translate(0%, 0%);
    transition-delay: 0.15s;
}
@media screen and (max-width: 1023px) {
    .ms-cc_p {
        display: none;
    }
}