.ms-posts--wrap {
    overflow: hidden;
}
.ms-posts--list,
.ms-posts--default {
    
    a + .post-content,
    .ms-post-media__link + .post-content,
    .ms-post-media__audio + .post-content,
    .ms-post-media__quote + .post-content,
    .ms-post-media__video + .post-content,
    .ms-post-media__gallery + .post-content {
        padding: var(--space-md) 0 0;
    }
    .grid-item {
        width: 100%;
        position: relative;
        margin-bottom: 8vh;

        .post-category__list {
            display: flex;
            flex-wrap: wrap;
            font-size: 14px;
            margin-bottom: 30px;
            color: var(--color-contrast-medium);
            span {
                margin-right: 6pt;
            }
            .post-categories {
                list-style: none;
                padding-left: 0;
                display: flex;
                flex-wrap: wrap;
                margin-bottom: 8pt;
            }

            a {
                color: var(--color-contrast-higher);
                display: flex;
                margin-bottom: 4pt;
                &::after {
                    content: '';
                    display: block;
                    position: absolute;
                    background-color: var(--color-primary);
                    bottom: 0;
                    left: 0;
                    width: 100%;
                    height: 1px;
                    z-index: -1;
                    transition: height .3s cubic-bezier(.23,1,.32,1);
                }
                &:not(:first-child) {
                    margin-right: 6pt;
                }
                &:hover::after {
                    height: 4px;
                }
            }

        }

        .post-category__list.top {
            position: absolute;
            top: 12pt;
            left: 14pt;
            z-index: 2;
            margin-bottom: 0;
        }

        .post-footer {
            display: flex;
            align-items: flex-end;
        }
        .post-meta-header {
            display: flex;

            .post-meta__info {
                display: flex;
                flex-direction: column;
            }

            .card__header {
                display: flex;
                align-items: center;
                font-size: 14px;
                color: var(--color-contrast-medium);
                margin-top: 4pt;

                .ms-p--ttr {
                    display: flex;
                    align-items: center;
                }
                .ms-p--ttr::before {
                    content: '';
                    width: 3pt;
                    height: 3pt;
                    margin: 2pt 8pt 0;
                    border-radius: 50%;
                    background-color: hsla(var(--color-contrast-medium-h),var(--color-contrast-medium-s),var(--color-contrast-medium-l),.45);
                }
            }
        }

        h2 {
            font-weight: 800;
            transition: color 150ms var(--ease-in-out);
            word-wrap: break-word;
            max-width: 100%;
            word-break: break-all;
            margin-top: 8pt;

            &:hover {
                color: var(--color-primary);
            }

        }
        img {
            border-radius: 24pt 24pt;
        }
        p {
            margin-top: 16pt;
            margin-bottom: 16pt;
            font-size: 1rem;
        }

    }

    .grid-item:last-child {
        border-bottom: none;
    }

    .post-footer {

        .post-footer--link {
            text-transform: capitalize;
        }
    }

    a + .post-footer {
        margin-top: 8pt;
    }
}

// Elementor
.ms-posts--wrap {
    .ms-posts--list,
    .ms-posts--default {
        padding: 0;
    }
    .ms-posts--list {
        .grid-item h2 {
            word-break: normal;
            font-size: 24px;
        }
    }
}
.ms-posts--default {
.post-header {
    display: flex;
    font-size: 14px;
    font-weight: bold;

    .post-separator {
        margin: 0 4pt 4pt;
        opacity: .4;
    }
}
    .post-top + .post-meta {
        margin-top: 0;
    }
}

.ms-single-post,
.ms-posts--card,
.ms-posts--card_2,
.ms-posts--default,
.ms-posts--list {

    .ms-sticky.no-thumbnail {
        position: relative;
        top: 0;
        left: 0;
    }
    .ms-sticky {
        text-transform: uppercase;
        background-color: var(--color-warning);
        border-radius: 50px;
        padding: 4pt 16pt;
        display: flex;
        font-size: 12px;
        align-items: center;
        justify-content: center;
        line-height: 1.8;
        color: var(--color-black);
    }

    .post-meta-date {
        display: flex;
        align-items:flex-start;
        align-content:flex-start;
        color: var(--color-contrast-medium);
        span:not(:last-child) {
            flex-shrink: 0;
        }

    }
    .post-meta-date.meta-date-sp {

        .post-author__name {
            position: relative;
            display: flex;
            align-items: center;
        }
        .post-author__name::after {
            content: "";
            display: block;
            margin-top: 1pt;
            width: 3pt;
            height: 3pt;
            margin: 2pt 8pt 0;
            border-radius: 50%;
            background-color: hsla(var(--color-contrast-medium-h),var(--color-contrast-medium-s),var(--color-contrast-medium-l),.45);
        }


    }
    h3 {
        word-wrap: break-word;
    }

    p {
        line-height: var(--body-line-height);
    }

    .post-meta-date {

        .post-category a:first-child::before {
            content: '';
            position: absolute;
            top: calc(50% - 1pt);
            left: -.85rem;
            height: 5px;
            width: 5px;
            display: block;
            background-color: var(--color-contrast-low);
            border-radius: 50%;
        }
    }

}

.ms-pc2--thumb {
    .ms-sticky {
        padding: 0;
    }
}

.ms-sticky.thumbnail {
    position: absolute;
    top: -2px;
    right: -2px;
    z-index: 1;
    font-weight: 700;
    border-radius: 24pt;
    border: solid var(--color-bg);
    border-top-left-radius: 0;
    border-bottom-right-radius: 0;
}

// Search Page
.ms-sp-list {

    .grid-item {
        margin-bottom: 0;

        h3 {
            border-bottom: 0;
            padding-bottom: 0;
        }
        p {
            margin-top: var(--space-sm);
        }
    }

}
.ms-sp--title span,
.search-word {
    font-weight: 400;
    color: var(--color-contrast-medium);
}
.search-results {

    .post-meta__date {
        font-size: 14px;
        display: block;
        margin-bottom: 8pt;
        color: var(--color-contrast-medium);
    }

    .post-footer--link {
        margin-left: 0 !important;
    }
}
// No results
.search-again-block {
    margin-top: calc(1.1 * var(--space-md));
    display: flex;
    align-items: center;
    p {
        margin-top: 0 !important;
        border-bottom: none !important;
        padding-bottom: 0 !important;
        margin-bottom: 0 !important;
        margin-right: var(--space-md);
    }
}

@media (min-width: 992px) {
    .ms-sidebar.pl-lg-5,
    .ms-sb-wc.pl-lg-5 {
        padding-left: 4rem !important;
    }
}
@media (max-width: 975px) {

    .ms-posts--default a+.post-content {
        padding: var(--space-md) 0;
    }

    .ms-related-posts {
        article + article + article {
            margin-top: var(--space-xl);
        }
    }
    // Index Page
    .ms-posts--default.col-lg-8:not(.no-result) {
        padding-left: 0;
        padding-right: 0;
    }

    .ms-single-post {
        .ms-sp--article,
        .entry-footer,
        .single-post__tags,
        .post-navigation {
            padding-right: calc(var(--bs-gutter-x)/ 2)!important;
            padding-left: calc(var(--bs-gutter-x)/ 2)!important;
        }
    }
    .ms-related-posts {
        padding-right: calc(var(--bs-gutter-x)/ 2)!important;
        padding-left: calc(var(--bs-gutter-x)/ 2)!important;       
    }
    .ms-posts--default .grid-item h2 {
        font-size: var(--text-lg);
    }
}

.no-result p {
    margin-top: 0 !important;
}
@media (max-width: 768px) {
    .ms-related-posts {
        article + article {
            margin-top: var(--space-xl);
        }
    }
    .ms-p--ttr {
        display: none !important;
    }
}

// Single Post

.single-post__share {
    text-align: center;
    h3 {
        font-size: 22px;
        font-weight: 700;
    }

    .share-box__icon {
        width: max-content;
        margin: 0 auto;
        display: flex;
        place-content: center;
        align-items: center;
        display: flex;
        flex: 0 0 auto;
        flex-flow: row;
        gap: 0px;
        height: min-content;
        // overflow: hidden;
        height: 45px;
        position: relative;
        will-change: width;
        padding-left: 15px;
        padding-right: 15px;
        backface-visibility: hidden;
        background-color: var(--color-contrast-lower);
        --border-radius-p: 40px;
        clip-path: inset(0% calc(50% - 22px) 0% calc(50% - 22px) round var(--border-radius-p) var(--border-radius-p) var(--border-radius-p) var(--border-radius-p));
        transition: max-width .6s cubic-bezier(.23,1,.32,1), background-color .6s cubic-bezier(.23,1,.32,1);
        transition: background-color .6s cubic-bezier(.38,.005,.215,1), clip-path .5s cubic-bezier(.38,.005,.215,1);
        
        .dots-icon {
            position: absolute;
            margin: auto;
            border-radius: 40px;
            display: flex;
            align-items: center;
            justify-content: center;
            font-size: 24px;
            width: 45px;
            height: 45px;
            flex-direction: row;
            color: var(--color-contrast-higher);
            transition: all .6s cubic-bezier(.23,1,.32,1);
        }
        &:hover {
            --border-radius-p: 40px;
            clip-path: inset(0% 0% 0% 0% round var(--border-radius-p) var(--border-radius-p) var(--border-radius-p) var(--border-radius-p));
            background-color: var(--color-contrast-high);
            
            .dots-icon {
                opacity: 0;
                will-change: transform;
                backface-visibility: hidden;
                transition: all .1s cubic-bezier(.23,1,.32,1);
            }
            a {
                opacity: 1;
                transform: scale(1);
            }
        }
    }

    .share-button-wrapper {
        display: flex;
        justify-content: center;
        align-items: center;

        a {
            font-size: 18px;
            width: 45px;
            height: 45px;
            display: flex;
            align-items: center;
            justify-content: center;
            margin: 0;
            border-radius: 40px;
            color: var(--color-contrast-lower);
            opacity: 0;
            transition: all .6s cubic-bezier(.23,1,.32,1);
            will-change: transform;
            backface-visibility: hidden;
            border: solid 1px transparent;
            &:hover {
                color: var(--color-primary);
            }
        }

        .share-copy {
            &::before {
                content: attr(data-message);
                font-size: 12px;
                padding: 1pt 4pt;
                border-radius: 40px;
                background-color: var(--color-primary);
                color: var(--color-contrast-higher);
                position: absolute;
                bottom: calc(100% + 6pt);
                opacity: 0;
                transform: translateY(40px);
                transition: all .3s cubic-bezier(.23,1,.32,1);
            }
        }
        .share-copy.copied::before {
            opacity: 1;
            transform: translateY(40px);
        }

    }
}