input::-moz-placeholder,textarea::-moz-placeholder {
    opacity: .5 !important;
}

input::placeholder,textarea::placeholder {
    opacity: .5 !important;
}

.ms-mc4wp--wrap {
  border-radius: 24pt;

  h2 {
    margin-bottom: calc(var(--space-unit) * 0.25 * var(--text-vspace-multiplier,1));
  }

  p {
    line-height: 24px;
  }
  .form-control {
    background: var(--color-bg);
  }
  .ms-mc4wp--action {
    display: flex;
    align-items: center;
    justify-content: center;

.ms-mc4wp--ac {
  display: flex;
  width: 100%;
  position: relative;

      .form-control {
        margin-right: 0px;
        padding-right: 135px;
      }
      input[type=submit] {
        position: absolute;
        top: 2px;
        right: 2px;
        bottom: 2px;
        height: calc(100% - 4px);
        font-size: 16px;
        border: none;
        color: var(--color-white);
        background-color: var(--color-primary);
      }
    }

  }
}

// Sidebar Style
.ms-sidebar {
    .ms-mc4wp--wrap {
        padding: 20px 24px;
        background-color: var(--color-contrast-lower);

        .btn {
            padding-left: 0;
            padding-right: 0;
        }
    }
    .ms-mc4wp--action {
        margin-bottom: 5px;
    }
}
  .ms-mc4wp--wrap {
    p {
        margin-bottom: 20px;
    }
    .btn {
        width: 45px;
        min-width: 45px;
        height: 45px;
        margin-left: 4pt;
        .ms-btn__text {
            display: flex;
        }
        svg {
            width: 24px;
            transform: rotate(90deg);
            transition: fill .3s ease;
        }
        &:hover {
            svg {
                fill: var(--color-contrast-lower);
            }
        }
    }
    .ms-mc4wp--content {
      width: 100%;
    }

    .ms-mc4wp--action {
      width: 100%;
    }

  }
