.portfolio_wrap {
    overflow: hidden;
    .subnav {
        margin-bottom: var(--space-xl);
    }

    .subnav {
        z-index: 4;
        display: flex;
        border-radius: 8px;
        position: relative;
        z-index: 2;
    }
    .portfolio-feed {
        position: relative;

        .media-wrapper--auto {
            padding-bottom: 100%;
        }

        .load_filter {
            background: var(--color-contrast-high);
            position: absolute;
            top: 0;
            z-index: 1;
            width: 50px;
            height: 50px;
            border-radius: 50%;
            left: 50%;
            opacity: 0;
            transform: translateX(-50%) translateY(0%);
            display: flex;
            visibility: hidden;
            padding: 0;
            justify-content: center;
            transition: opacity .3s cubic-bezier(.575,.015,0,.995), transform .3s cubic-bezier(.575,.015,0,.995), visibility .3s cubic-bezier(.575,.015,0,.995);
            .load-filter-icon {
                position: absolute;
            }
            .load-filter-icon:last-child {
                stroke: var(--color-primary);
            }
        }

        .load_filter.show {
            opacity: 1;
            visibility: visible;
            transform: translateX(-50%) translateY(150%);
            transition: opacity .3s cubic-bezier(.575,.015,0,.995), transform .3s cubic-bezier(.575,.015,0,.995), visibility .3s cubic-bezier(.575,.015,0,.995);
        }

        .item--inner {
            position: relative;

            img {
                width: 100%;
                height: 100%;
                display: flex;
                object-fit: cover;
            }
            .plyr__video-wrapper {
                background-color: var(--color-bg);
            }
            .ms-vp__poster {
                position: absolute;
                top: 0;
                left: 0;
                right: 0;
                bottom: 0;
                z-index: 1;
                transition: opacity .3s;
            }
            .ms-p-content {
                display: block;
                flex-direction: column;
                will-change: transform;

                .ms-p-content__inner {
                    display: flex;
                    margin-top: 1.5rem;
                    justify-content: normal;
                }

                h3 {
                    font-size: 20px;
                    text-transform: uppercase;
                    font-size: 24px;
                    font-weight: 800;
                    transition: color .3s cubic-bezier(.1,0,.3,1);
                }
                .ms-p-cat {
                    color: var(--color-contrast-medium);
                    font-size: 16px;
                    display: block;
                }
    
            }

            &:hover .ms-p-content {
                opacity: 1;
                transition: opacity .5s .1s;
                h3 {
                    letter-spacing: 0;
                }
            }

        }

        .item--inner:hover {
            .ms-vp__poster {
                opacity: 0;
                transition: opacity .3s
            }
        }
        .fadein .ms-p-content,
        .overlay .ms-p-content,
        .mokko .ms-p-content {
            position: absolute;
            top: 0;
            left: 0;
            padding: var(--space-md);
            width: 100%;
            z-index: 1;
            width: 100%;
            display: flex;
            height: 100%;

            .ms-p-content__inner {
                margin-top: 0;
                width: 100%;
            }
                h3 {
                    padding-top: 0;
                    color: var(--color-white);
                }
                .ms-p-cat {
                    color: var(--color-white);
                    font-size: 16px;
                    font-weight: 400;
                    transform: translateX(0) translateY(0%);
                }
        }
        .fadein .ms-p-content,
        .overlay .ms-p-content,
        .mokko .ms-p-content {
            // Align
            &.align-left{
                align-items: flex-start;
            }
            &.align-center{
                align-items: center;
            }
            &.align-right{
                align-items: flex-end;
            }
            // Justify
            &.justify-top {
                justify-content: flex-start;
            }
            &.justify-center {
                justify-content: center;
            }
            &.justify-bottom {
                justify-content: flex-end;
            }
        }
        .mokko .item--inner > a {
            display: block;
        }
        .fadein .ms-p-content {
            h3::after {
                content: none;
            }
        }

        // FadeIn
        .fadein {

            .ms-p-content {
                opacity: 0;
                backface-visibility: hidden;
                will-change: transform;
                transition: opacity .3s;
                h3 {
                    font-size: 24px;
                    backface-visibility: hidden;
                }
            }

            &:hover .ms-p-content {
                opacity: 1;
                transition: opacity .3s;
                h3 {
                    letter-spacing: 0;
                }
            }

        }
        .fadein, .overlay {
            .item--inner a {
                display: block;
            }
           .item--inner figure::after {
                content: '';
                position: absolute;
                top: 0;
                left: 0;
                width: 100%;
                height: 100%;
                z-index: 1;
            }
        }

        // Boxed
        .boxed .ms-p-content {
            margin-top: -.1pt;
            padding: var(--space-sm);
            background-color: var(--color-contrast-lower);

            .ms-p-content__inner {
                display: flex;
                margin-top: 0;
                justify-content: normal;
                &.right {
                    justify-content: space-between;
                }
                &.bottom {
                    flex-direction: column;
                }
                &.top {
                    flex-direction: column-reverse;
                }
            }
                h3 {
                    padding-top: 0;
                }

        }

        .fadein:not(.center) {

           .item--inner figure::after {
                content: '';
                opacity: 0;
                transition: opacity .5s var(--ease-in-out);
            }

            &:hover .item--inner figure::after {
                opacity: 1;
            }

        }

        // FadeIn Center
        .fadein.center {

           .item--inner figure::after {
                content: '';
                opacity: 0;
                transition: opacity .3s cubic-bezier(.1,0,.3,1);
            }

            &:hover .item--inner figure::after {
                opacity: 1;
            }
            
        }

        // FadeIn Bottom
        .h-align-middle .ms-p-content {
            margin: auto auto;
            top: 0;
            bottom: 0;
            display: flex;
            justify-content: center;
        }
        .h-align-bottom .ms-p-content {
            top: auto;
            bottom: 0;
        }  
        // Hover Effect
        .grid-item-p figure {
            overflow: hidden;
            position: relative;
        }

        // Mokko Style
        .mokko {
            overflow: hidden;
            .ms-p-content {
                
                .ms-p-content__inner {
                    filter: url("#goo2");
                    display: inline !important;
                    font-size: initial !important;
                }
                .ms-p-content__inner h3 {
                    position: relative;
                    -webkit-box-decoration-break: clone;
                    box-decoration-break: clone;
                    display: inline;
                    padding: 0.5rem 1rem 0.5rem;
                    color: var(--color-contrast-higher);
                    background-color: var(--color-bg);
                    border-radius: 40px;
                    font-size: 24px;
                    line-height: 1.5;
                    &:focus {
                        outline: 0;
                    }
                }

                .ms-p-cat {
                    margin-top: 0;
                    align-self: start;
                    justify-self: start;
                    border: 1px solid;
                    border-radius: 1em;
                    line-height: 0.75;
                    padding: 0.5rem 0.75rem;
                    text-transform: capitalize;
                    letter-spacing: 1px;
                    width: max-content;
                    margin: 8pt 0;
                }
                
            }
            figure::after {
                content: '';
                position: absolute;
                top: 0;
                left: 0;
                width: 100%;
                height: 100%;
                z-index: 1;
                background-color: rgba(0,0,0,.15);
            }
        }
    }
    
    .ms-p--m figure {
        display: flex;

        img {
            float: left;
            object-fit: cover;
            object-position: 50% 50%;
            transition: object-position .3s var(--ease-in-out);
        }
    }

    .ajax-area {
        margin-top: var(--space-xl);
        margin-bottom: 4px;
    }
}


// Text Above
.portfolio_wrap .portfolio-feed .above {
    .ms-p-content__inner {
        margin-top: 0 !important;
    }
    .ms-p-content__inner.right {
        flex-direction: row !important;
        align-items: flex-start;
        justify-content: space-between !important;
        font-size: 2.8rem;
        font-weight: 400;
        gap: 1.6rem;
    }
    .ms-p-content__inner.top {
        flex-direction: column-reverse;
        .ms-p-cat {
            margin-bottom: 4pt;
        }
    }
    .ms-p-content__inner.bottom {
        flex-direction: column !important;
        .ms-p-cat {
            margin-top: 0px;
        }
    }
    .ms-p-arrow {
        bottom: 15px;
        top: auto;
    }
    .item--inner {
        a {
            display: flex;
            flex-direction: column-reverse;
        }
        .ms-p-content {
            display: flex !important;
            flex-direction: column-reverse !important;
            margin-bottom: 18px;
        }
    }
}
// Text Below
.overlay .item--inner,
.fadein .item--inner,
.below .item--inner {
    transition: transform 250ms ease;
    transform: translateY(0);

    .ms-p-img {
        position: relative;
    }

    .ms-p-content__inner.right {
        display: flex;
        align-items: flex-start;
        justify-content: space-between !important;
        font-size: 2.8rem;
        font-weight: 400;
        gap: 1.6rem;
    }
    .ms-p-content__inner.top {
        flex-direction: column-reverse;
        .ms-p-cat {
            margin-bottom: 10px;
        }
    }
    .ms-p-content__inner.bottom {
        flex-direction: column;
        .ms-p-cat {
            margin-top: 10px;
        }
    }

}

.overlay .item--inner:hover,
.below .item--inner:hover {

    .ms-p-img::after {
        opacity: 1;
        top: 0rem;
    }
}

// Default
.grid-item-p:not(.fadein) .ms-p-content,
.ms-p--m .ms-p-content {
    display: flex;
    flex-direction: column;
    h3 {
    position: relative;
    display: inline-block;
    }
}
.ms-p--g2 .grid-item-p,
.ms-p--d .grid-item-p,
.ms-p--m .grid-item-p {
    &:hover h3::after {
            -webkit-transform-origin: left center;
            -ms-transform-origin: left center;
            transform-origin: left center;
            -webkit-transform: scale(1,1);
            -ms-transform: scale(1,1);
            transform: scale(1,1);
        }
}

// Hover Arrow Animation
.ms-p-arrow {
    position: absolute;
    transform: rotate(45deg) scale(0);
    z-index: 1;
    right: 15px;
    top: 15px;
    background-color: var(--color-bg);
    width: 45px;
    height: 45px;
    border-radius: 40px;
    display: flex;
    align-items: center;
    justify-content: center;
    overflow: hidden;
    will-change: transform;
    transition: transform .4s cubic-bezier(.4,0,0,1);

    svg {
        width: 28px;
        fill: var(--color-contrast-higher);
    }

}
.grid-item-p .item--inner:hover {
    .ms-p-arrow {
        transform: rotate(45deg) scale(1);
    }
    .ms-p-arrow svg {
        animation: MoveScaleUpInitial .3s forwards, MoveScaleUpEnd .3s .2s forwards;
    }
}


// Parallax Effect
.ms-p-img.parallax,
.ms-vp__poster.parallax {
    &.p_t {
        --speed_h: calc(100% + (940% * var(--speed)));
        --speed_m: calc(-250% * var(--speed));
    }
    &.p_b {
        --speed_h: calc(100% - (940% * var(--speed)));
        --speed_m: calc(250% * var(--speed));
    }

    img {
        height: var(--speed_h)!important;
        margin-top: var(--speed_m);
    }

}

.filter-nav--expanded .filter-nav__btn {
    background-color: transparent;
}

// Grid Style 2
.ms-p--g2 {
    --aspect: 4/3.2;
    .grid-item-p:nth-child(4n),
    .grid-item-p:nth-child(4n-1) {
        --aspect: 2/1.1;
    }
    .ms-p-img,
    .ms-p-video {
        aspect-ratio: var(--aspect);
    }
    .ms-p-video {
        display: flex;
        position: relative;
        justify-content: center;
        video {
            height: 100%;
            object-fit: cover;
            max-width: none !important;
            max-width: none;
        }
        &.parallax {
            video {
                height: calc(100% + 10vw) !important;
                margin-top: -4.6vw;
                position: absolute;
            }
        }
    }
}
// Single Portfolio Page

// Navigation
.ms-spn--wrap {
    margin: 3rem auto 8vh;

    .ms-spn--text {
        text-transform: uppercase;
        font-weight: bold;
        margin: 3rem 0;
        
        h3 {
            text-transform: uppercase;
            color: var(--color-contrast-medium);
            font-size: 25px;
        }
    }
    .ms-spn--content {
        height: 100%;
        display: flex;
        flex-direction: column;
        position: relative;
        align-items: center;
        justify-content: center; 

        .col-md-4 {
            text-align: center;
        }
        
        .ms-spn--link {
            position: relative;
            z-index: 2;
            font-size: 0.8vw;
            text-transform: none;

            h1 {
                text-transform: uppercase;
                color: var(--color-contrast-higher);
                font-weight: 900;
                transition: color .3s;
            }
            .ms-spn--head {
                display: flex;
                align-items: center;
                margin-bottom: 8pt;
                justify-content: center;

                h3 {
                    font-size: 48px;
                }

            }

        }

        .ms-spn--thumb {
            height: 100%;
            aspect-ratio: auto 1 / 1;
            margin: auto;
            border-radius: 24pt;
            overflow: hidden;
            display: flex;
            flex-direction: column;
            will-change: transform;
            transition: all 1s cubic-bezier(.575,.015,0,.995);

            img {
                object-fit: cover;
                object-position: center;
                width: 100%;
                height: 100%;
                will-change: transform;
                transition: margin-top 1s cubic-bezier(.575,.015,0,.995), transform 1s cubic-bezier(.575,.015,0,.995);
            }

        }

        .ms-spn--title {
            display: inline-flex;
            margin: 20px 0;
            
            h1 {
                font-size: 24px;
            }
        }

        a h1 {
            &::after {
                content: '';
                display: block;
                position: absolute;
                background-color: var(--color-primary);
                bottom: 0;
                left: 0;
                width: 100%;
                height: 1px;
                z-index: -1;
                -webkit-transition: height .3s cubic-bezier(.23,1,.32,1);
                transition: height .3s cubic-bezier(.23,1,.32,1);
            }
        }

        a:hover h1::after {
            height: 4px;
        }

    }

}
@media (max-width: 1921px) {
    
    .ms-spn--wrap .ms-spn--content .ms-spn--link {
        font-size: 1vw;
    }

}

@media (max-width: 1024px) {
    .ms-spn--wrap .ms-spn--content {
        min-height: auto;
    }
    .ms-spn--wrap .ms-spn--text h3 {
        font-size: 1.4rem;
    }
    .ms-spn--link h1 {
        font-size: 2rem;
    }
}

// Aspect Ratio fixed

// Masonry Video Thumb
.custom-ratio.grid-item-p {
    .ms-p-video {
        display: flex;
        justify-content: center;
        overflow: hidden;
        position: relative;
        video {
            object-fit: cover;
            display: flex;

        }
        &.parallax {
            video {
                height: calc(100% + 15vh) !important;
                margin-top: -8vh
            }
        }
    }
}

// Dark Mode
body[data-theme='dark'] {
    .portfolio_wrap .portfolio-feed .mokko .ms-p-content .ms-p-content__inner h3 {
        color: var(--color-contrast-lower);
        background-color: var(--color-white);
    }
}