// MS Accordion Widget
.ms_accordion {
  overflow: hidden;

  .ms_ac_panel {
    position: relative;
  }
  .ms_ac--label {
    color: var(--color-contrast-higher);
    
    cursor: pointer;
    display: flex;
    justify-content: space-between;
    align-items: center;
    user-select: none;
  }

  .ms_ac--icon {
    display: flex;
    position: relative;
    align-self:stretch;

    .accordion_icon--open,
    .accordion_icon--close {
        display: flex;
        width: 24px;
        align-items: center;
        justify-content: center;
    }
    .accordion_icon--close {
        position: absolute;
        right: 0;
        top: 50%;
        transform: translateY(-50%); 
    }

    svg {
        fill: var(--color-contrast-high);
    }
  }

  .ms_ac--content {
    overflow: hidden;

    p:last-child {
        margin-bottom: 30px;
    }
  }

  &.i--left {
    .ms_ac--label {
        display: flex;
        flex-direction: row-reverse;
        justify-content: flex-end;

        .label-title {
            margin-left: 8pt;
        }
    }
  }

  &.l-ef {
    .ms_ac--label {
        &::before,
        &::after {
            display: block;
            content: '';
            width: 100%;
            position: absolute;
            z-index: 1;
            bottom: 0;
            border-bottom: solid 1px;
        }
        &::before {
            border-color: var(--color-contrast-higher);
        }
        &::after {
            display: block;
            content: '';
            width: 100%;
            transform: scaleX(0);
            transform-origin: 100% 50%;
            border-bottom: solid 1px var(--color-primary);
            transition: transform .5s cubic-bezier(.4,0,.2,1);
        }
        &:hover::after {
            transform: scaleX(1);
            
            transform-origin: 0% 50%;
        }
    }
}

}