/* -------------------------------- 

Title: Comments Style
Descr: Design Comments

-------------------------------- */
.ms-section__comments {
  width: 100%;
  max-width: var(--responsive--aligndefault-width);
  margin-left: auto;
  margin-right: auto;
  position: relative;
  padding: calc(var(--space-lg) + 20px) 0;
  
  .ms-comments-title {
    font-weight: 900;
    font-size: calc(var(--text-xl,1.728em) + 1px);
    margin-bottom: var(--space-sm);
    font-family: var(--font-heading);
  }
  .required-field-message {
    margin-left: 4pt;
  }
}
.form-submit .btn {
  border-radius: 4px;
}
.ms-comment-list {
  padding-left: 0;
  width: 100%;
  position: relative;
  margin-bottom: var(--space-lg);
  list-style: none;

  a {
    color: var(--color-contrast-higher);
        &::after {
            content: '';
            display: block;
            position: absolute;
            background-color: var(--color-primary);
            bottom: 0;
            left: 0;
            width: 100%;
            height: 1px;
            z-index: -1;
            -webkit-transition: height .3s cubic-bezier(.23,1,.32,1);
            transition: height .3s cubic-bezier(.23,1,.32,1);
        }
      &:hover::after {
        height: 4pt;
    }
  }

}
.ms-author-vcard--info {
  display: flex;
  flex-direction: column;

  .ms-author-name {
    font-size: 16px;
    font-weight: 700;
    line-height: 1.2;
  }
  .ms-comment-time {
    font-size: 15px;
    margin-top: 2pt;
    color: var(--color-contrast-medium);
  }
}

.ms-comment-body {
  font-size: var(--text-sm);
  position: relative;
  margin: 30px 0;
  display: flex;
  padding: 20px 0;
}
.ms-comment-body:last-child {
  margin-bottom: 0;
}
.ms-author-vcard {
  margin-right: 20px;
  position: relative;
}
.ms-author-vcard-content {
  width: 100%;
}
.ms-commentcontent {
  margin-top: calc(var(--global--spacing-vertical) - 10px);
  font-size: 16px;
  margin-right: 2pt;
  line-height: 1.8;
  border-radius: 8px;
  > *:not(blockquote p) {
    margin-bottom: calc(var(--global--spacing-vertical) - 10px);
  }
  img {
    padding: 6pt 0;
  }
}
.ms-comment-footer {
  display: flex;
  align-items: center;
  margin-bottom: 0;
}
.comment-edit-link,
.ms-comment-footer a {
  margin-right: var(--space-xxxs);
  font-size: 16px;
  text-decoration: none;
  display: flex;
  transition: opacity 250ms ease;
  svg {
    margin-right: var(--space-xxxs);
  }
}
.ms-comment-edit a {
  margin-right: 12pt;
}
.comment-reply-link {
  .group {
    fill: var(--color-contrast-higher);
    margin-right: var(--space-xxxs);
  }
}
.ms-comment-footer div:last-child a {
  margin-right: 0;
}
.ms-comment-list .children {
  margin-left: var(--space-lg);
  list-style: none;
}
.comment-notes {
  font-size: 14px;
  color: var(--color-contrast-medium);
}
.comment-notes,
.logged-in-as {
  margin-top: 15px;
  margin-bottom: 30px;
}
.comment-form-cookies-consent {
  font-size: 14px;
  display: flex;
  width: 100%;
  align-items: center;
  margin-bottom: 30px;

    #wp-comment-cookies-consent {
      margin-right: 8pt;
      margin-top: 2pt;
    }
}
.post.pingback {
  margin: var(--space-md) 0;
  .post-date {
    font-size: 14px;
    color: var(--color-contrast-medium);
    margin-bottom: var(--space-sm);
  }
}
#reply-title {
  font-weight: 900;
}
li + .comment-respond {
  margin-top: 30px;
}

#cancel-comment-reply-link {
  letter-spacing: 0;
  padding-left: var(--space-xxs);
  font-size: 16px;
  font-weight: 400;
  color: var(--color-error);
  &:hover {
    opacity: .75;
  }
}

@media only screen and (max-width: 552px) {
  .ms-section__comments {
    max-width: inherit;
    padding-right: calc(var(--bs-gutter-x)/ 2)!important;
    padding-left: calc(var(--bs-gutter-x)/ 2)!important;   
  }
  .comment-notes {
    padding-left: 2pt;
    margin-top: 4pt;
  }
  .comment-form-cookies-consent {
    margin-left: 1.2rem;
    margin-bottom: 20px;
  }
  .avatar.avatar--lg {
    display: none;
  }
  .ms-author-vcard {
    margin-left: 0;
  }
  .ms-comment-list .children {
    margin-left: 0;
  }
}

// Links
.logged-in-as a {
    color: var(--color-contrast-higher);
    &::after {
        content: '';
        display: block;
        position: absolute;
        background-color: var(--color-primary);
        bottom: 0pt;
        left: 0;
        width: 100%;
        height: 1px;
        z-index: -1;
        transition: height .3s cubic-bezier(.23,1,.32,1);
      }
    &:hover::after {
        height: 4pt;
    }
}

// Button
.btn--comments {
    font-size: 1rem;
    padding-left: 1.25rem;
    padding-right: 1.25rem;
    padding-top: 9px;
    padding-bottom: 9px;
    border-radius: 40px;
    font-weight: 600;
    height: 45px;
    position: relative;
    overflow: hidden;
    border: none;
    color: var(--color-contrast-higher);
    background-color: var(--color-primary);
    transition: background-color .3s cubic-bezier(.645,.045,.355,1), transform .1s cubic-bezier(.645,.045,.355,1);
    span:first-child {
        position: relative;
        z-index: 2;
        transition: color .4s cubic-bezier(.23,1,.32,1);
    }
    .link_overlay {
        content: '';
        position: absolute;
        width: 140%;
        height: 140%;
        border-radius: 50%;
        top: -20%;
        left: -20%;
        z-index: 1;
        transform: translateY(87%);
        display: inline-block;
        background-color: var(--color-contrast-higher);
        will-change: transform;
        transition: transform .4s cubic-bezier(.23,1,.32,1);
    }
  &:hover span {
    color: var(--color-white);
    translate: none;
    rotate: none;
    scale: none;
    transform: translate(0,0);
  }
  &:active {
    transform: translateY(2px);
  }
}